/**
 * SVG Template
 * Do not edit this file, used for gulp svg plugin
 */
@mixin icon-svg {
	background-image: url(asset('sprite-icon-aeb32a2d.svg'));
	background-repeat: no-repeat;
	background-size: em(400) em(4823);
	content: "";
	display: block;
	font-size: em(16);
}

@mixin icon-svg-inline {
	display: inline-block;
	position: relative;
	vertical-align: middle;
}

.icon {
	display:inline-block;

	&::before {
		@include icon-svg;
	}
}

.icon--inline {
	&::before {
		@include icon-svg-inline;
	}
}

