/* Components */
/*
///////////////////////////////////////////////////////////
Product View General
///////////////////////////////////////////////////////////
*/
.pv__hdg {
  font-size: 1.75em;
  line-height: 1.35714em;
  margin: 0;
}

@media only screen and (min-width: 768px) {
  .pv__hdg {
    font-size: 2.25em;
    line-height: 1.44444em;
  }
}

.fb-messenger-checkbox {
  display: none !important;
}

.license-tilt {
  transform: rotate(-5deg);
}

/*
///////////////////////////////////////////////////////////
Essentials
///////////////////////////////////////////////////////////
*/
@media only screen and (min-width: 1025px) {
  .pv-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 1.875rem 0 5rem;
  }
}

@media only screen and (max-width: 1024px) {
  .pv-wrap {
    padding: 0 4rem 2.25rem;
  }
}

@media only screen and (max-width: 767px) {
  .pv-wrap {
    padding: 0 0.625rem 2.25rem;
  }
}

.pv-essentials {
  padding-top: 0.9375em;
  position: relative;
}

@media only screen and (min-width: 1025px) {
  .pv-essentials {
    padding-top: 0;
  }
}

.pv-photos {
  z-index: 0;
}

@media only screen and (max-width: 1024px) {
  .pv-photos .ir__fill--top {
    align-items: center;
  }
}

@media only screen and (min-width: 1025px) {
  .pv-gallery {
    width: 56.6%;
  }
  .pv-details {
    width: 43.4%;
    padding: 0 2.75em;
    transition: padding 400ms ease-in-out;
  }
}

.pv-actions {
  padding-top: 0.625rem;
}

@media only screen and (max-width: 480px) {
  .pv-actions {
    padding-top: 0rem;
  }
}

.p-share {
  padding-top: 0.9375rem;
}

.pv-links {
  margin-top: 3.125rem;
}

.pv-links a {
  border-bottom: 1px solid #dfdfdf;
}

.pv-add-btn {
  font-size: 0.75rem;
  padding: 1.0625rem 0.625rem 1rem;
}

@media only screen and (min-width: 768px) {
  .pv-add-btn {
    font-size: 0.875rem;
  }
}

/*
///////////////////////////////////////////////////////////
Essentials
///////////////////////////////////////////////////////////
*/
@media only screen and (min-width: 1025px) {
  .pv-details {
    padding-top: 2.5em;
  }
}

@media only screen and (max-width: 1024px) {
  .pv-details {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
}

.pv-desc {
  font-size: 0.875rem;
}

@media only screen and (max-width: 1024px) {
  .pv-desc {
    order: 2;
    padding-top: 3.75rem;
  }
}

@media only screen and (max-width: 767px) {
  .pv-desc {
    padding-top: 1.875rem;
  }
}

.pv-desc p {
  line-height: 1.6;
}

@media only screen and (max-width: 767px) {
  .pv-desc p:last-child {
    margin-bottom: 0;
  }
}

.pv-header {
  padding-bottom: 1.3125rem;
}

@media only screen and (max-width: 480px) {
  .pv-header {
    padding-bottom: 1.5625rem;
  }
}

.pv-stock {
  font-size: 1.125em;
}

.pv-price {
  font-size: 1em;
  font-weight: 500;
  margin: 0;
}

@media only screen and (min-width: 768px) {
  .pv-price {
    font-size: 1.125em;
  }
}

.pv-price__compare-at {
  font-size: 0.9375rem;
}

.pv-title {
  color: #ed3fa8;
  font-size: 1.625rem;
  margin: 0 0 0.375rem;
  position: relative;
}

@media only screen and (min-width: 768px) {
  .pv-title {
    font-size: 2.375rem;
  }
}

.pv-addon {
  align-items: flex-start;
  display: flex;
  margin: 0.9375rem 0 1.25rem;
  width: 100%;
}

@media only screen and (min-width: 1025px) {
  .pv-addon {
    width: 27.5rem;
  }
}

.pv-addon__bg {
  background-color: #f9f9f9;
  display: flex;
  flex-direction: column;
  flex-grow: 3;
  min-height: 5rem;
  justify-content: center;
  padding: 0.75rem 0.4375rem 0.75rem 1.875rem;
}

@media only screen and (min-width: 768px) {
  .pv-addon__bg {
    min-height: 6.25rem;
  }
}

.pv-addon__bg h4 {
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 1.125rem;
  margin: 0 0;
  padding: 0 0;
  text-align: left;
}

.pv-addon__bg h4 span {
  color: #ed3fa8;
  text-decoration: underline;
}

.pv-addon__bg label:before {
  background-color: #fff;
  border: 1px solid #dfdfdf;
  border-radius: 50%;
  /*styles outer circle*/
  content: " ";
  display: inline-block;
  height: 15px;
  margin: 0 7px 0 0;
  position: relative;
  top: 3px;
  width: 15px;
}

.pv-addon__bg label {
  color: #ed3fa8;
  font-size: 0.8125rem;
  margin-top: 0.4375rem;
  position: relative;
}

@media only screen and (min-width: 1281px) {
  .pv-addon__bg label {
    font-size: 0.875rem;
  }
}

.pv-addon__bg label input[type='checkbox'] {
  display: none;
  /*removes original button*/
}

.pv-addon__bg label input[type='checkbox']:checked + span.pink {
  background-color: #ed3fa8;
  /*styles inside circle*/
  border-radius: 50%;
  display: block;
  height: 0.5625rem;
  left: 3px;
  padding-top: 6px;
  position: absolute;
  top: 6px;
  width: 0.5625rem;
}

.pv-addon__bg label span {
  font-size: 0.8125rem;
  font-weight: 500;
}

@media only screen and (min-width: 768px) {
  .pv-addon__bg--spooky-bag {
    min-height: 5rem;
  }
}

.pv-addon__bg--spooky-bag h4 {
  color: #ed3fa8;
}

.pv-addon__bg--spooky-bag p {
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 0;
}

.pv-addon__img {
  min-height: 5rem;
  width: 4.5rem;
}

@media only screen and (min-width: 768px) {
  .pv-addon__img {
    min-height: 6.25rem;
    width: auto;
  }
}

/**
* gallery
*/
.pv-gallery {
  position: relative;
}

@media only screen and (min-width: 1025px) {
  .pv-gallery {
    display: flex;
  }
  .pv-gallery .flickity-page-dots {
    display: none;
  }
}

@media only screen and (max-width: 1024px) {
  .pv-gallery {
    margin-bottom: 1.5625rem;
  }
}

/**
* photos
*/
.pv-photos {
